import React from 'react';
import styles from './Advantages.module.css'
import busIcon from '../../images/bus-icon-min.png'
import scheduleIcon from '../../images/schedule-icon-min.png'
import timeIcon from '../../images/time-icon-min.png'
import wifiIcon from '../../images/wifi-icon-min.png'
import servicesIcon from '../../images/services-icon-min.png'
import teaIcon from '../../images/tea-icon-min.png'

const Advantages = () => {
    return (
        <div className={styles.container}>
            <h2 className={styles.title}>    ?</h2>
            <ul className={styles.list}>
                <li className={styles.listItem}>
                    <div className={styles.imgBox}><img src={busIcon} alt=' '></img></div><p> </p>
                </li>
                <li className={styles.listItem}>
                    <div className={styles.imgBox}><img src={scheduleIcon} alt=' '></img></div><p> </p>
                </li>
                <li className={styles.listItem}>
                    <div className={styles.imgBox}><img src={timeIcon} alt=' '></img></div><p>   </p>
                </li>
                <li className={styles.listItem}>
                    <div className={styles.imgBox}><img src={teaIcon} alt='   '></img></div><p>    </p>
                </li>
                <li className={styles.listItem}>
                    <div className={styles.imgBox}><img src={wifiIcon} alt='wifi  '></img></div><p>WiFi</p>
                </li>
                <li className={styles.listItem}>
                    <div className={styles.imgBox}><img src={servicesIcon} alt='  '></img></div><p> ()</p>
                </li>
            </ul>
        </div>
    );
};

export default Advantages;